import React, { useEffect } from "react";
import webLogo from "../../assets/img/web-logo.png";
import marc from "../../assets/img/faces/marc.jpg";
import location from "../../assets/img/location-header.png";
import { NavLink, useHistory } from "react-router-dom";
// import * as Scroll from "react-scroll";
import { Link } from "react-scroll";
import { useDispatch, useSelector } from "react-redux";
import createAction from "redux/Actions";
import { IS_LOGIN } from "redux/Constants/UserConstants";
import { FETCH_DETAIL_USER } from "redux/Constants/UserConstants";

export default function Header() {
  const history = useHistory();
  const dispatch = useDispatch();
  const isLogin = useSelector((state) => state.credential.isLogin);
  const dataLogin = useSelector((state) => state.credential.dataLogin);

  useEffect(() => {
    dispatch(createAction(FETCH_DETAIL_USER, { isDetail: false }));
  }, [dispatch]);

  return (
    <header>
      <nav className="navbar navbar-expand-md navbar-light tixNav">
        <a href="/#" className="navbar-brand" onClick={() => history.push("/")}>
          <img src={webLogo} alt="logo" />
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarTixContent"
          aria-controls="navbarTixContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
          // onClick={handleScroll}
        >
          <span className="navbar-toggler-icon" />
        </button>
        <div className="collapse navbar-collapse" id="navbarTixContent">
          <ul className="navbar-nav">
            <li className="nav-item first">
              <Link
                className="nav-link"
                to="tixCarousel"
                spy={true}
                smooth={true}
                offset={0}
                duration={1000}
                delay={0}
              >
                Lch Chiu
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link"
                to="tixCinema"
                spy={true}
                smooth={true}
                offset={0}
                duration={1000}
                delay={0}
              >
                Cm rp
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link"
                to="tixNews"
                spy={true}
                smooth={true}
                offset={-65}
                duration={1000}
                delay={0}
              >
                Tin Tc
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link"
                to="tixApp"
                spy={true}
                smooth={true}
                offset={-65}
                duration={1000}
                delay={0}
              >
                ng dng
              </Link>
            </li>
            <li className="nav-item user">
              {isLogin ? (
                <>
                  <a href="/#" className="nav-link">
                    <img src={marc} alt="user" /> {dataLogin.hoTen}
                  </a>
                  <div className="logout">
                    <NavLink to="/information" style={{ color: "white" }}>
                      Profile
                    </NavLink>
                    {dataLogin.maLoaiNguoiDung === "QuanTri" ? (
                      <NavLink to="/admin/user" style={{ color: "white" }}>
                        System Admin
                      </NavLink>
                    ) : (
                      <br />
                    )}
                    <a href="/#"
                      style={{ color: "white" }}
                      onClick={() => {
                        localStorage.removeItem("currentUser");
                        localStorage.removeItem("accessToken");
                        history.push("/")
                        dispatch(createAction(IS_LOGIN, false));
                      }}
                    >
                      ng Xut
                    </a>
                  </div>
                </>
              ) : (
                <NavLink to="/login" className="nav-link" href="#random">
                  <img src={marc} alt="user" /> ng Nhp
                </NavLink>
              )}
              {/* <NavLink to="/admin">Qun tr h thng</NavLink> */}
            </li>
            <li className="nav-item dropdown">
              <a
                className="nav-link dropdown-toggle"
                href="#random"
                id="navbarTixDropdown"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <img className="location" src={location} alt="location" />
                H Ch Minh
              </a>
              <div
                className="dropdown-menu"
                aria-labelledby="navbarTixDropdown"
              >
                <a className="dropdown-item" href="#random">
                  H Ch Minh
                </a>
                <a className="dropdown-item" href="#random">
                  H Ni
                </a>
                <a className="dropdown-item" href="#random">
                   Nng
                </a>
                <a className="dropdown-item" href="#random">
                  Hi Phng
                </a>
                <a className="dropdown-item" href="#random">
                  Bin Ha
                </a>
                <a className="dropdown-item" href="#random">
                  Nha Trang
                </a>
                <a className="dropdown-item" href="#random">
                  Bnh Dng
                </a>
                <a className="dropdown-item" href="#random">
                  Phan Thit
                </a>
                <a className="dropdown-item" href="#random">
                  H Long
                </a>
                <a className="dropdown-item" href="#random">
                  Cn Th
                </a>
                <a className="dropdown-item" href="#random">
                  Vng Tu
                </a>
                <a className="dropdown-item" href="#random">
                  Nha Trang
                </a>
                <a className="dropdown-item" href="#random">
                  Quy Nhn
                </a>
                <a className="dropdown-item" href="#random">
                  Hu
                </a>
                <a className="dropdown-item" href="#random">
                  Long Xuyn
                </a>
                <a className="dropdown-item" href="#random">
                  Thi Nguyn
                </a>
                <a className="dropdown-item" href="#random">
                  Bun Ma Thut
                </a>
                <a className="dropdown-item" href="#random">
                  Bc Giang
                </a>
                <a className="dropdown-item" href="#random">
                  Bn Tre
                </a>
                <a className="dropdown-item" href="#random">
                  Vit Tr
                </a>
                <a className="dropdown-item" href="#random">
                  Ninh Bnh
                </a>
                <a className="dropdown-item" href="#random">
                  Thi Bnh
                </a>
                <a className="dropdown-item" href="#random">
                  Vinh
                </a>
                <a className="dropdown-item" href="#random">
                  Bo Lc
                </a>
                <a className="dropdown-item" href="#random">
                   Lt
                </a>
                <a className="dropdown-item" href="#random">
                  Tr Vinh
                </a>
                <a className="dropdown-item" href="#random">
                  Yn Bi
                </a>
                <a className="dropdown-item" href="#random">
                  Kin Giang
                </a>
                <a className="dropdown-item" href="#random">
                  Vnh Long
                </a>
                <a className="dropdown-item" href="#random">
                  C Mau
                </a>
                <a className="dropdown-item" href="#random">
                  Hu Giang
                </a>
                <a className="dropdown-item" href="#random">
                  Ty Ninh
                </a>
                <a className="dropdown-item" href="#random">
                  Tuyn Quang
                </a>
                <a className="dropdown-item" href="#random">
                  Thanh Ha
                </a>
                <a className="dropdown-item" href="#random">
                  Nam nh
                </a>
                <a className="dropdown-item" href="#random">
                  Hi Dng
                </a>
                <a className="dropdown-item" href="#random">
                  Gia Lai
                </a>
                <a className="dropdown-item" href="#random">
                  H Tnh
                </a>
                <a className="dropdown-item" href="#random">
                  Ph Yn
                </a>
                <a className="dropdown-item" href="#random">
                  Bc Liu
                </a>
                <a className="dropdown-item" href="#random">
                  Long An
                </a>
                <a className="dropdown-item" href="#random">
                  ng Thi
                </a>
                <a className="dropdown-item" href="#random">
                  H Nam
                </a>
                <a className="dropdown-item" href="#random">
                  Bc Ninh
                </a>
                <a className="dropdown-item" href="#random">
                  Qung Tr
                </a>
                <a className="dropdown-item" href="#random">
                  Kon Tum
                </a>
                <a className="dropdown-item" href="#random">
                  Sc Trng
                </a>
                <a className="dropdown-item" href="#random">
                  Sn La
                </a>
                <a className="dropdown-item" href="#random">
                  Lng Sn
                </a>
                <a className="dropdown-item" href="#random">
                  Qung Ngi
                </a>
                <a className="dropdown-item" href="#random">
                  M Tho
                </a>
                <a className="dropdown-item" href="#random">
                  ng Thp
                </a>
                <a className="dropdown-item" href="#random">
                  Hng Yn
                </a>
              </div>
            </li>
          </ul>
        </div>
      </nav>
    </header>
  );
}
